.basic-button {
    color: black;
    text-decoration: none;
    background-color: white;
    font-family: var(--primary-text-font);
    padding: 1.1vh 2vw;
    border-radius: 10px;
    font-size: 1.3vw;
    width: fit-content;
    height: fit-content;
}

.basic-button {
    transition: opacity 0.3s ease-in-out;
    opacity: 1;
}

.basic-button:hover {
    transition: opacity 0.3s ease-in-out;
    opacity: 0.8;
}